@charset "UTF-8";
/*
  Hoja de reset basada en:
    <https://beta.adalab.es/resources/stylesheets/reset.css>
    <https://meyerweb.com/eric/tools/css/reset/>

  NOTA: Ning칰n 치rbol ha sido talado en la elaboraci칩n
        de esta hoja de estilos 游꺕
*/
:root {
  font-size: 16px; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  line-height: 1; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
  font: inherit;
  vertical-align: baseline; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.page__main {
  width: 100%;
  height: 100vh;
  background-color: #be525b; }

.form {
  width: 100%;
  padding: 30px 0 0 30px; }

.form__input {
  height: 30px;
  border-radius: 10px;
  border: none;
  padding-left: 10px; }

.form__button {
  margin-left: 10px;
  padding: 0 10px;
  background-color: #892232;
  border-radius: 10px;
  border: none;
  height: 30px;
  cursor: pointer; }
  .form__button:hover {
    background-color: #800000;
    transform: scale(1.1); }

.main__list {
  width: 100%;
  height: auto;
  padding: 30px 0 120px 30px; }

.page__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  width: 100%;
  height: 120px;
  background-color: #892232; }

.header__title {
  font-size: 32px;
  padding-left: 30px; }

.header__logo {
  width: 140px;
  height: 100px;
  border-radius: 50%;
  margin-left: 30px; }

.page__footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 50px;
  background-color: #892232;
  box-sizing: content-box; }

.footer__copy {
  position: relative;
  top: 10px;
  padding-left: 30px; }
